import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import Footer from '../../containers/footer/Footer';
import Menu from '../../containers/menu/Menu';

import Hamburger from '../../components/hamburger/Hamburger';
import HomeButton from '../../components/buttons/HomeButton';

class Spedity extends Component {
  componentDidMount() {
    window.sr.reveal(".cover", window.cover);

    window.sr.reveal('.immagine', window.immagini);
    window.sr.reveal('.image', window.immagini);

    window.sr.reveal(".herotitle", window.herotitle);
    window.sr.reveal(".description", window.herotitle);

    window.sr.reveal(".text", window.text);

    window.sr.reveal(".sectitle", window.sectitle);
    window.sr.reveal(".freccia", window.arrow);
    window.sr.reveal('.qanda', window.qanda, 100);
    window.sr.reveal('.project', window.projects, 100);
    window.sr.reveal(".contact", window.contact);
    window.sr.reveal(".footer", window.footer, 120);
  }

  // componentWillUnmount() {
  //   setTimeout(() => window.location.reload(), 100);
  //   setTimeout(() => window.scrollTo(0, 0), 110);
  // }

  render() {
    const { history } = this.props;
    return (
      <div id="main">
        <Menu />
        <div className="container">
          <Hamburger />
          <HomeButton />
        </div>

        <div className="projectpage">
          {/* <a className="freccia" href="#list"></a> */}
          <div className="hero spedity">
            <div className="container">
              {/* <div id="page-title" className="home hidden">
                <a href="../../en/index.html" className="name">mono. <span>studio</span></a>
              </div> */}
            	<h1 onClick={() => history.push('/')} className="brand" style={{ cursor: 'pointer' }}>laon.</h1>
            </div>
          </div>

          <div className="container paragrafo">
            <h2 className="projecttitle herotitle col-xs-10 col-sm-8 mx-auto">Spedity is a free, fast and secure application that brings together carriers with shippers.</h2>
            <p className="description col-xs-10 col-sm-8 mx-auto">We designed a platform that allows users to set up a request in few seconds, easily and quickly. Users can find and organize all their deliveries and manage all their documents in a tidy and distraction-free interface.</p>
          </div>

          <div className="img_full secondcover">
            <img className="immagine" src="/images/projects/spedity/00_cover_spedity.png" alt="spedity Cover" />
          </div>

          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">We worked to create design guidelines that easily communicate to a broader target audience, improving the values behind the company. These guidelines allow discovering new attributes: quality, authenticity and approachability.</p>
          </div>
          <div className="img_background">
            <img className="immagine" src="/images/projects/spedity/03_design_guidelines.png" alt="spedity Design guidelines" width="780px" height="1017px" />
          </div>

          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">We used two distinct type of icon sets. The first, a standard set of line icons, has the sole purpose of represents actions to help the platform’s navigation.</p>
          </div>
          <div className="img_background">
            <img className="immagine" src="/images/projects/spedity/04_icon_set.png" alt="Spedity Icon Set" width="1137px" height="309px" />
          </div>

          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">The second set of icon was designed by ourself to represent each type of vehicles in the “Transport request” section of the platform and on the map's results. As the platform supports different vehicles, the point was creating a homogeneous family of iconographic illustrative elements for an immediate association.</p>
          </div>
          <div className="img_background">
            <img className="immagine" src="/images/projects/spedity/05_icon_set_map.png" alt="Spedity Icon Set Map" width="1170px" height="296px" />
          </div>

          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">We believe we created an awesome user interface that gives hierarchy and consistency. It is bold, tidy and readable at the same time.</p>
          </div>
          <div className="img_background">
            <img className="immagine" src="/images/projects/spedity/06_ui_kit.png" alt="Spedity UI Kit" width="1193px" height="2763px" />
          </div>

          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">Each request has a colour that changes based on the status of the delivery: black for “Scheduled", light blue for ”On retreat”, purple for "On delivery”. When the order is delivered, the color is green.</p>
          </div>
          <div className="img_background">
            <img className="immagine" src="/images/projects/spedity/07_status.png" alt="Spedity request status" width="1164px" height="353px" />
          </div>

          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">The user can easily have access to his dashboard and have full informations about each request like its time of arrival or its historic.</p>
          </div>
          <div className="img_background">
            <img className="immagine" src="/images/projects/spedity/08_dashboard_screen.png" alt="Spedity dashboard" width="1210px" height="3665px" />
          </div>

          <div className="container paragrafo">
            <p className="text col-xs-10 col-sm-8 mx-auto">We designed a landing page that gives a clear message for a complex product. We also realized some landing pages to convince users to become a driver or a partner.</p>
          </div>
          <div className="img_background">
            <img className="immagine" src="/images/projects/spedity/10_landing.png" alt="Spedity landing" width="1440px" height="2412px" />
          </div>

          <div className="img_full">
            <img className="immagine" src="/images/projects/spedity/09_sitescreen.jpg" alt="Spedity sitescreens" />
          </div>
        </div>

        <div className="container">
          <Footer />
        </div>
      </div>
    );
  }

}

export default withRouter(Spedity);



